.n-wrapper{
    height: 10vh;
    display: flex;
    justify-content: space-between;

}
.n-left{
    flex:1 1;
    align-items: center;
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
}
.n-name{
    font-size: 1.3rem;
    font-weight: bold;
}
.n-right{
    display: flex;
    flex:1 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}
.n-list>ul{
    display: flex;
    grid-gap:2rem;
    gap:2rem;
    margin-right: 4rem;
    cursor: pointer;
    
}
.n-list>ul>li:hover{
    cursor: pointer;
    color: var(--orange);
    color: var(--orange);
}
.n-button{
    flex: 2 1;
    
}
@media screen and (max-width:480px){
    .n-list{
        display: none;
    }
    .n-button{
        display: none;
    }
}


:root
{
  --yellow: #F5C32C;
    --orange : #FCA61F;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App{
    padding: 0.5rem 3.5rem;
    overflow: hidden;
    color: #242D49;
    color: var(--black);    
}
.button{
  border-radius: 34px;
  border:none;
  color: white;
  font-size: 16px;
  padding: 11px 26px 11px 26px;
  background: linear-gradient(180deg,#fdc50f 26.71%,#fb982f 99.36%);
  box-shadow: 0px 20px 24px 3px rgba(251,161,40,0.42);

}
.button:hover{
  background: white;
  cursor: pointer;
  color: #FCA61F;
  color: var(--orange);
  border: 1px solid #FCA61F;
  border: 1px solid var(--orange);
}
@media screen and (max-width:480px){
  .App{
    padding: 0.5rem 1rem;
  }
}


  

  
.intro{
    display: flex;
    height: 77vh;
    margin-top: 6rem;
}
.i-left{
    display: flex;
    position: relative;
    flex: 1 1;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
}

.i-name{
    display: flex;
    flex-direction: column;
}
.i-name>:nth-child(1){
   color: var(--black);
   font-weight: bold;
   font-size: 3rem; 
}
.i-name>:nth-child(2){
  color: var(--orange);
  font-weight: bold;
  font-size: 3rem;  
}
.i-name>:nth-child(3){
    font-weight: 100;
    font-size: 14px;
    margin-top: 10px;
    color: var(--gray);
}
.i-button{
width: 6rem;
height: 2rem;
}
.i-icons{
    margin-top: 3rem;
    display: flex;
    grid-gap: 0rem;
    gap: 0rem;
}
.i-icons>*{
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}
.i-icons:hover{
    cursor: pointer;
    
}

.i-right{
    flex: 1 1;
    position: relative;
}
.i-right>*{
    position: absolute;
    z-index: 1;
}
.i-right>:nth-child(1){
    -webkit-transform: scale(0.69);
            transform: scale(0.69);
    left: -15%;
    top:-9%;
}
.i-right>:nth-child(2){
    top: -4.6rem;
    -webkit-transform: scale(0.67);
            transform: scale(0.67);
    left: -3rem;
}
.i-right>:nth-child(3){
    -webkit-transform:scale(1.2) ;
            transform:scale(1.2) ;
    left: 28%;
}
.i-right>:nth-child(4){
-webkit-transform: scale(0.6);
        transform: scale(0.6);
top:-19%;
left: -24%;
border-radius: 50%;
padding: 0;
}
.blur{
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    z-index:-9;
    top: -18%;
    left: 56%;
    -webkit-filter: blur(72px);
            filter: blur(72px);

}
@media  screen and (max-width:480px) {
    .intro{
        flex-direction: column;
        height: 64rem;
        grid-gap: 7rem;
        gap: 7rem;
    }
    .i-right{
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
        left: -3rem;
    }
    .i-right .blur{
        display: none;
    }
    .floating-div:nth-of-type(1){
        top:-7rem !important
    }
    
}
.floatingdiv{
    justify-content: space-around;
    display: flex;
    background: white;
    box-shadow: var(--boxshadow);
    border-radius:17px ;
    align-items: center;
    padding: 0px 28px 0px 0px;
    height: 4.5rem;
}
.floatingdiv>img{
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
}
.floatingdiv>span{
    font-family: sans-serif;
    font-size: 16px;
}
.services{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-bottom: 8rem;
    margin-top: 9rem;
    margin-bottom: 13rem;
}
.awesome{
    display: flex;
    flex-direction: column;
    position: relative;
}
.awesome>:nth-child(1){
    color: var(--black);
    font-size: 2.5rem;
    font-weight: bold;
}
.awesome>:nth-child(2){
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}
.awesome>:nth-child(3){
    color: var(--gray);
    font-size: 14px;
    margin-top: 1rem;
}
.s-button{
    width: 8rem;
    height: 2rem;
    margin-top: 1rem;
}
.cards{
    position: relative;
}
.cards>*{
    position: absolute;
}
.s-blur2{
    left: 14rem;
    top: 8rem;
}
.s-blur1{
    top:13rem;
    left:-18rem;
}
@media screen and (max-width:480px) {
    .services{
        margin-top: 0;
        flex-direction: column;
        grid-gap: 5rem;
        gap: 5rem;
        height: 66rem;
        padding: 0rem;
    }
    .cards{
        display: flex;
        flex-direction: column;
        grid-gap: 17rem;
        gap: 17rem;
    }
    .cards>*{
        position: static;
    }
    
}
.card{
    width: 10rem;
    height: 13rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: center;
    width: 10rem;
    text-align: center;
    background: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--orangeCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    padding: 0px 26px 2rem 26px;
}
.card span:nth-of-type(2){
    color: var(--gray);
    font-size: 16px;
}
.card>img{
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    margin-bottom: -2rem;
}
.c-button{
    background: FFFFFF;
    box-shadow: 0px 19px 60px rgba(0, 0, 0, 0.08);
    border-radius: 7px;
    border: none;
    padding: 10px;
    font-size: 16px;
    color: #5290FD;
}
@media screen and (max-width:480px){
    .card{
        width: auto;
    }
}
.experience{
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 7rem;
    gap: 7rem;
    height: 30vh;
    margin-bottom: 8rem;
}
.achievement{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.circle{
    width: 4rem;
    height: 4rem;
    background: white;
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 2rem;

}
.circle::before{
    border-radius: 100%;
    content: '';
    background-image: linear-gradient(to bottom,#87e6fb,rgb(252, 204, 110));
    top: -8px;
    left: -8px;
    bottom: -8px;
    right: -8px;
    position: absolute;
    z-index: -1;
    box-shadow: var(--boxShadow);
}
.achievement span:nth-of-type(2){
    color: var(--orange);
}
.achievement span:nth-of-type(3){
    color: var(--gray);
    font-size: medium;
    
}
.achievement:nth-of-type(2){
    padding-top: 0.8rem;
    padding-left: 1rem;
}
.works{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;
}
.w-right{
    position: relative;
}
.w-mainCircle{
    position: relative;
    left: 9rem;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top: 2rem;
    background: white;
}
.w-mainCircle>*{
    position: absolute;
}
.w-mainCircle>:nth-child(1){
    top: -3rem;
    left: 6rem;
}
.w-mainCircle>:nth-child(2){
top: 5rem;
left: -3rem;
}
.w-mainCircle>:nth-child(3){
    top: 5rem;
    left: 6rem;
}
.w-mainCircle>:nth-child(4){
        top: 5rem;
        left: 15rem;
 }
.w-mainCircle>:nth-child(5){
            top: 14rem;
            left: 6rem;
            
}
.w-secCircle{
    width: 6rem;
    height: 6rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    box-shadow: var(--smboxShadow);
    background: white;
}


.w-secCircle>img{
    -webkit-transform: scale(.8);
            transform: scale(.8);
}
.w-backCircle{
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    z-index: -1;
}
.blueCircle{
    background: #1949B8;
    left: 18rem;
    top: 0rem;
}
.yellowCircle{
    background: #F5C32C;
    left: 18rem;
    top: 8rem;
}
@media screen and (max-width:480px){
    .works{
        flex-direction: column;
        height: 55rem;
        padding: 0;
        grid-gap: 5rem;
        gap: 5rem;
    }
    .w-right{
        display: flex;
        align-items: center;
        justify-content: start;
        -webkit-transform: scale(0.7);
                transform: scale(0.7);
    }
    .w-mainCircle{
        position: static;
    }
    .w-backCircle{
        left: 8rem !important;
    }
}
.portfolio{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
