.experience{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 7rem;
    height: 30vh;
    margin-bottom: 8rem;
}
.achievement{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.circle{
    width: 4rem;
    height: 4rem;
    background: white;
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 2rem;

}
.circle::before{
    border-radius: 100%;
    content: '';
    background-image: linear-gradient(to bottom,#87e6fb,rgb(252, 204, 110));
    top: -8px;
    left: -8px;
    bottom: -8px;
    right: -8px;
    position: absolute;
    z-index: -1;
    box-shadow: var(--boxShadow);
}
.achievement span:nth-of-type(2){
    color: var(--orange);
}
.achievement span:nth-of-type(3){
    color: var(--gray);
    font-size: medium;
    
}
.achievement:nth-of-type(2){
    padding-top: 0.8rem;
    padding-left: 1rem;
}